import { AuthCallbackClient } from '@/components/client/auth';

interface CallbackPageProps {
  params: Promise<{ provider: string }>;
}

export default async function CallbackPage({ params }: CallbackPageProps) {
  // Next.js 15 ÏöîÍµ¨ÏÇ¨Ìï≠: paramsÎ•º awaitÎ°ú unwrap
  const { provider } = await params;
  
  return (
    <>
      {/* Ï†ÑÏó≠ Î°úÎî©Î∞î Ï¶âÏãú ÏãúÏûëÏùÑ ÏúÑÌïú Ïä§ÌÅ¨Î¶ΩÌä∏ */}
      <script
        dangerouslySetInnerHTML={{
          __html: `
            // OAuth ÏΩúÎ∞± ÌéòÏù¥ÏßÄ ÏßÑÏûÖ Ï¶âÏãú Ï†ÑÏó≠ Î°úÎî©Î∞î ÏãúÏûë
            (function() {
              console.log('üîÑ [OAuth Callback] ${provider} ÏΩúÎ∞± ÌéòÏù¥ÏßÄ ÏßÑÏûÖ - Ï†ÑÏó≠ Î°úÎî©Î∞î ÏãúÏûë');
              
              // Ï†ÑÏó≠ Î°úÎî©Î∞î Ï¶âÏãú ÌëúÏãú
              try {
                // GlobalLoadingContextÏóê Ï†ëÍ∑ºÌï¥ÏÑú Î°úÎî© ÏÉÅÌÉú ÏÑ§Ï†ï
                const event = new CustomEvent('startGlobalLoading', { detail: { reason: 'oauth-callback' } });
                window.dispatchEvent(event);
                console.log('üöÄ [OAuth Callback] Ï†ÑÏó≠ Î°úÎî©Î∞î Ïù¥Î≤§Ìä∏ Î∞úÏÜ° ÏôÑÎ£å');
              } catch (error) {
                console.warn('‚ö†Ô∏è [OAuth Callback] Ï†ÑÏó≠ Î°úÎî©Î∞î Ïù¥Î≤§Ìä∏ Î∞úÏÜ° Ïã§Ìå®:', error);
              }
              
              // ÏûÑÏãú Î°úÎî© ÌëúÏãú (Ï†ÑÏó≠ Î°úÎî©Î∞î Î∞±ÏóÖ)
              const loadingDiv = document.createElement('div');
              loadingDiv.id = 'oauth-loading';
              loadingDiv.style.cssText = \`
                position: fixed;
                top: 0;
                left: 0;
                width: 100vw;
                height: 100vh;
                background: #f9fafb;
                display: flex;
                align-items: center;
                justify-content: center;
                z-index: 9998;
                opacity: 0;
                transition: opacity 0.2s ease-in-out;
              \`;
              
              loadingDiv.innerHTML = \`
                <div style="text-align: center; color: #6b7280;">
                  <div style="margin-bottom: 16px;">üîÑ</div>
                  <div>Î°úÍ∑∏Ïù∏ Ï≤òÎ¶¨ Ï§ë...</div>
                </div>
              \`;
              
              // Î∂ÄÎìúÎü¨Ïö¥ fade-in
              if (document.body) {
                document.body.appendChild(loadingDiv);
                setTimeout(() => { loadingDiv.style.opacity = '1'; }, 10);
              } else {
                document.addEventListener('DOMContentLoaded', function() {
                  document.body.appendChild(loadingDiv);
                  setTimeout(() => { loadingDiv.style.opacity = '1'; }, 10);
                });
              }
            })();
          `
        }}
      />

      {/* ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ Ïª¥Ìè¨ÎÑåÌä∏ */}
      <AuthCallbackClient provider={provider} />
    </>
  );
}
